<template>
  <div class="photoChange">
    <img :src="img"
         ref="img"
         alt="">
    <div class="btn">
      <div @click="$emit('close')"></div>
      <div @click="onConfirm"></div>
    </div>
  </div>
</template>

<script>
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import { editPhoto } from '@/api/editUserInfo'
export default {
  props: {
    img: String
  },
  data () {
    return {
      cropper: null
    }
  },
  mounted () {
    const image = this.$refs.img
    this.cropper = new Cropper(image, {
      viewMode: 1, // 
      dragMode: 'none', // 
      aspectRatio: 1, // 
      // autoCropArea: 1, // 
      background: false // 
    })
  },
  methods: {
    // 
    onConfirm () {
      this.$toast.loading({
        message: '',
        forbidClick: true,
        duration: 0
      })
      // console.log(this.cropper.getData())
      this.cropper.getCroppedCanvas().toBlob(async (blob) => {
        // console.log(blob)
        // Content-Typemultipart/form-data
        //  photo:blob
        const formData = new FormData()
        formData.append('photo', blob)
        try {
          const { data: res } = await editPhoto(formData)
          // console.log(res)
          // 
          this.$emit('close')
          // 
          this.$emit('updataPhoto', res.data.photo)
          this.$toast.success('')
        } catch (err) {
          this.$toast('')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.photoChange {
  background-color: #000;
  height: 100%;
  img {
    max-width: 100%;
  }
  .btn {
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    div {
      width: 90px;
      height: 90px;
      font-size: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
    }
  }
}
</style>
